header {
  width: 100%;
  background-image: url("../img/Фон.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
header nav {
  padding: 85px 0px;
}
header nav .box {
  display: flex;
  justify-content: right;
  align-items: center;
  padding-bottom: 25px;
}
@media (max-width: 991px) {
  header nav .box {
    width: 100%;
    justify-content: left;
    margin-top: 20px;
  }
}
header nav .box img {
  margin: 0px 10px;
}
header nav .box span {
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  color: white;
}
header nav .box span:hover {
  cursor: pointer;
  transition: 1s;
  color: #DBA069;
}
header nav .bars {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
header nav .bars:hover {
  border-color: #DBA069;
  color: #DBA069;
}
header nav .bars .fa {
  font-size: 30px;
}
header nav ul {
  display: flex;
  justify-content: right;
  align-items: center;
}
@media (max-width: 991px) {
  header nav ul {
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
  }
}
header nav ul li {
  padding: 0px 20px;
}
@media (max-width: 991px) {
  header nav ul li {
    padding-bottom: 5px;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    margin: 5px 0px;
  }
}
header nav ul li a {
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: white;
  letter-spacing: 1px;
}
header nav ul li a:hover {
  cursor: pointer;
  transition: 1s;
  color: #DBA069;
}
header .headerTitle {
